export const labelDataBuilder = (
  params = {
    id: '',
    productName: '',
    isMac: false,
    detail: '',
    condition: '',
    buybackId: '',
    imei: null,
    price: ''
  }
) => {
  // const maxChars = 75
  let originalText = `${params.productName || ''} ${
    params.isMac == 1 ? params.detail : ''
  } / ${params.condition || ''}`
  
  originalText = splitToNewLine(originalText, LABEL_MAX_CHAR_LENGTH, { useSuffix: true })
  
  let mainText = `${params.productName || ''} ${
    params.isMac == 1 ? params.detail : ''
  }`.trim()
  
  const conditionPart = ` / ${params.condition || ''}`
  const totalText = mainText + conditionPart
  
  let limitedText
  if (totalText.length <= LABEL_MAX_CHAR_LENGTH) {
    limitedText = totalText
  } else {
    const maxMainTextLength = 65 - conditionPart.length - 3 // -3 for "..."
    const truncatedMainText = mainText.length > maxMainTextLength 
      ? mainText.substring(0, maxMainTextLength).trim() + '...'
      : mainText
    limitedText = truncatedMainText + conditionPart
  }
  
  limitedText = splitToNewLine(limitedText, LABEL_MAX_CHAR_LENGTH, { useSuffix: true })
  
  return {
    text: originalText,
    text_limited: limitedText,
    id: params.id
      ? params.id.startsWith?.('#')
        ? params.id
        : `#${params.id}`
      : 'N/A',
    code: params.imei || params.id || 'N/A',
    quote_id: params.buybackId || 'N/A',
    imei_sn: params.imei || 'N/A',
    price: params.price
  }
}

export const retailLabelDataBuilder = (
  params = {
    id: '',
    productName: '',
    condition: 'No Condition',
    color: '',
    isMac: false,
    detail: '',
    price: '',
    imei: null
  }
) => {
  let Text = `${params.productName} ${
    params.isMac == 1 ? params.detail : ''
  } ${params.color} / ${params.condition ? params.condition : 'No Condition'}`
  Text = splitToNewLine(Text, LABEL_MAX_CHAR_LENGTH, { useSuffix: true })

  let mainText = `${params.productName || ''} ${
    params.isMac == 1 ? params.detail : ''
  }`.trim()
  
  const conditionPart = ` / ${params.condition || ''}`
  const totalText = mainText + conditionPart
  
  let LimitedText
  if (totalText.length <= LABEL_MAX_CHAR_LENGTH) {
    LimitedText = totalText
  } else {
    const maxMainTextLength = 65 - conditionPart.length - 3 // -3 for "..."
    const truncatedMainText = mainText.length > maxMainTextLength 
      ? mainText.substring(0, maxMainTextLength).trim() + '...'
      : mainText
    LimitedText = truncatedMainText + conditionPart
  }
  
  LimitedText = splitToNewLine(LimitedText, LABEL_MAX_CHAR_LENGTH, { useSuffix: true })
  return {
    Text,
    LimitedText,
    Code: params.id
    ? params.id.startsWith?.('#')
      ? params.id
      : `#${params.id}`
    : 'N/A',
    Price: params.price,
    Barcode: params.imei || params.id,
    Imei: params.imei || 'N/A'
  }
}
